@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
:root {
    --rad: 0.7rem;
    --dur: 0.3s;
    --color-dark: #2f2f2f;
    --color-light: #fff;
    --color-brand: #57bd84;
    --font-fam: "Lato", sans-serif;
    --height: 5rem;
    --btn-width: 6rem;
    --bez: cubic-bezier(0, 0, 0.43, 1.49);
  }
body{
    background-color: #f5f5f5;
    background: #2f2f2f;
    background: var(--color-dark);
    height: 100vh;
    min-height: 100vh;
    display: grid;
    place-items: center;
}
.search{
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
}
.searchTerm{
    width: 50%;
    border: none;
    padding: 16px;
    height: 20px;
    border-radius: 5px 0 0 5px;
    outline: none;
    font-size: 18px;
}
.searchTerm:focus{
    color: #00b4cc;
}
.searchButton{
    width: 80px;
    height: 32.9px;
    border: none;
    background: #048b9c;
    text-align: center;
    color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 16px;
    outline: none;
}
.searchButton:hover{
    background-color: #17b8ce;

}
.wrap{
    display: flex;
    justify-content: center;
    text-align: center;
}
.widget{
    display: flex;
    height: 400px;
    width: 650px;
    margin-top: 50px;
    flex-wrap: wrap;
    cursor: pointer;
    border-radius: 20px;
    box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3),
      0 17px 17px 0 rgba(0, 0, 0, 0.15);
}
.widget .weathericon{
    flex: 1 1 100%;
    height: 50%;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background: #fafafa;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 100px;
    
}
.widget .weatherinfo{
    display: flex;
    flex: 0 0 70%;
    height: 40%;
    background: #080705;
    align-items: center;
    color: white;
}
.widget .weatherinfo .temperature{
    flex: 0 0 40%;
    display: flex;
    width: 100%;
    font-size: 65px;
    justify-content: space-around;
    margin-right: 20px;
    margin-left: 20px;
}
.widget .weatherinfo .description .weathercondition{
    text-transform: uppercase;
    font-size: 35px;
    font-weight: 100;
}
.widget .weatherinfo .description .place{
    font-size: 15px;
}
.widget .date{
    display: flex;
    flex: 0 0 30%;
    height: 40%;
    background: #70c1b3;
    justify-content: space-around;
    align-items: center;
    color: white;
    font-size: 30px;
    font-weight: 800;
    text-align: center;
}
.widget .extra-temp{
    display: grid;
    flex: 1 1 100%;
    padding: 5% 0;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background: #fafafa;
    grid-template-columns: repeat(2, 1fr);
}
.temp-info-minmax{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.weather-extra-info{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.two-sided-section{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    color: #70c1b3;
    text-align: center;
    align-items: center;

}
.widget .extra-temp p {
    font-size: 25px;
    font-weight: 600;
}
.widget .extra-temp .extra-info-leftside{
    text-align: left;
    font-weight: 600;
    text-transform: capitalize;
    color: #080705;
    font-size: 15px;
}


